<template>
  <div class="wrapper">
      <h1 class="todo-title">Todo List ({{total}})</h1>
      <input type="text" class="todo-input" name="" value="" placeholder="What needs to be done?" v-model="todoInp" @keyup.enter="addList" maxlength="50">
      <!-- <p>{{todoInp}}</p> -->

      <div class="todo-section">
          <ul class="todo-list">
              <li v-for="(t, index) in todoListFilter" :key="t.id">
                  <label>
                      <input type="checkbox" class="todo-check" v-on:change="setStorage" v-model="t.done">
                      <div class="todo-text">
                          <span @dblclick="editTodo(t)" v-if="!t.editing">{{ t.content }}</span>
                          <input type="text" class="todo-edit-inp" v-model="t.content" v-else @keyup.enter="doneEdit(t)" @blur="doneEdit(t)" @keyup.esc="cancelEdit(t)" >
                      </div>
                  </label>
                  <b @click="removeList(index)">&times;</b>
              </li>
          </ul>
      </div>

    <div class="extra-section" v-if="total > 0">
        <input type="checkbox" class="todo-check" v-model="isDone" @click="checkAll">
        <div class="selection">
            <small v-bind:class="[activeTab=='all'?'active':'']" @click="activeTab='all'">All</small>
            <small v-bind:class="[activeTab=='pending'?'active':'']" @click="activeTab='pending'">Pending</small>
            <small v-bind:class="[activeTab=='completed'?'active':'']" @click="activeTab='completed'">Completed</small>
        </div>
        <small class="todo-text">{{ remaining }} items left</small>
    </div>
  </div>
</template>

<script>
export default {
    name: 'TodoList',
    data () {
        return {
            storageKey: 'todo_items',
            todoInp: '',
            todoList: [],
            beforeEditText: '',
            activeTab: 'all',
        }
    },
    created() {
        this.todoList = JSON.parse(localStorage.getItem(this.storageKey) || []);
    },
    computed: {
        total() {
            return this.todoList.length;
        },
        remaining() {
            return this.todoList.filter(todo => !todo.done).length;
        },
        isDone() {
            return this.remaining == 0;
        },
        todoListFilter() {
            let selector = this.activeTab;

            if (selector=='all') {
                return this.todoList;
            }else if (selector=='pending') {
                return this.todoList.filter(todo => !todo.done);
            }else if (selector=='completed') {
                return this.todoList.filter(todo => todo.done);
            }

            return this.todoList;
        },
    },
    methods: {
        addList() {
            if (this.todoInp.trim().length == 0) {
                return ;
            }
            let l = {
                "id" : this.todoList.length,
                "content" : this.todoInp,
                "done" : false,
                "editing" : false,
            };

            this.todoInp = "";
            this.todoList.push(l);

            this.setStorage();
        },
        removeList(index) {
            this.todoList.splice(index, 1);

            this.setStorage();
        },
        editTodo(t) {
            this.beforeEditText = t.content;
            t.editing = true;

            this.setStorage();
        },
        doneEdit(t) {
            if (t.content.trim().length == 0) {
                t.content = this.beforeEditText;
            }
            t.editing = false;

            this.setStorage();
        },
        cancelEdit(t) {
            t.content = this.beforeEditText;
            t.editing = false;

            this.setStorage();
        },
        checkAll(event) {
            this.todoList.forEach((todo)=>todo.done = event.target.checked);

            this.setStorage();
        },
        setStorage() {
            localStorage.setItem(this.storageKey, JSON.stringify(this.todoList));
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
 .wrapper {
     width: 600px;
     margin: 0 auto;
     text-align: center;
 }

 .todo-input {
     border: 2px solid #0f2f50;
     border-radius: 10px;
     margin-bottom: 15px;
     width: 100%;
     height: 50px;
     padding: 0 10px;

     &:focus {
         outline: none;
         border: 2px solid #308fce;
     }
 }

 .todo-list {
     padding: 0;
     margin: 0;
     list-style: none;
     text-align: left;

     li {
        background-color: #efefef;
        display: flex;
        height: 50px;
        justify-content: center;
        align-items: center;
        border-bottom: 1px solid #ccc;
        animation: slideUp .3s ease-in-out;
     }

     label {
         display: flex;
         width: calc(100% - 45px);
         float: left;
         height: 100%;
         align-items: center;
         justify-content: center;
     }

     .todo-text {
         display: block;
         width: calc(100% - 45px);
         height: 100%;
         line-height: 50px;
     }

     .todo-edit-inp {
        width: 100%;
        height: 100%;
        font-size: 18px;
        font-family: "Courier New", cursive;
        padding: 0 5px;

        &:focus {
            outline: none;
        }
    }

    b {
        display: block;
        float: right;
        width: 25px;
        height: 25px;
        line-height: 25px;
        font-size: 25px;
        color: #fff;
        background-color: #0f2f50;
        border-radius: 50%;
        text-align: center;
        cursor: pointer;
        opacity: 0;
        transition: .6s linear;
        margin: 0 10px;
    }

    li:hover b {
        opacity: 1;
    }
 }

 .todo-check {
    text-align: center;
    width: 25px;
    height: 25px;
    border: none;
    -webkit-appearance: none;
    appearance: none;
    border: 2px solid #0f2f50;
    border-radius: 50%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    float: left;
    margin: 0 10px;
    outline: none;

    &:checked {
        border: 2px solid #0f2f50;
    }

    &:checked:after {
        content: "";
        display: block;
        color: #0f2f50;
        width: 15px;
        height: 15px;
        background-color: #0f2f50;
        border-radius: 50%;
    }

    &:checked + .todo-text span {
        text-decoration: line-through;
    }
 }

 .extra-section {
     display: flex;
     height: 50px;
     justify-content: center;
     align-items: center;
     border-top: 1px solid #ccc;
     border-bottom: 1px solid #ccc;
     animation: slideUp .3s ease-in-out;

     .selection {
         width: calc(75% - 45px);
         text-align: left;

         small {
             display: inline-block;
             padding: 3px 10px;
             background-color: #f0ecd4;
             border-radius: 4px;
             color: #000;
             cursor: pointer;

             &.active {
                 background-color: #8dd58f;
             }
         }
     }

     .todo-text {
         width: calc(25%);
         text-align: right;
     }
 }

 @keyframes slideUp {
     from {transform: translateY(15px); opacity: 0; }
     to {transform: translateY(0px); opacity: 1;}
 }
</style>



